import { RootState } from '../../../root.reducer';

export const getAllClinics = (state: RootState) => state.clinics.clinics;
export const getSelectedClinic = (state: RootState, selectedClinicId: string | null) => {
  const allClinics = state.clinics.clinics;
  const selectedClinic = allClinics.find(clinic => clinic.id === selectedClinicId);
  if (selectedClinic) {
    return selectedClinic;
  }
  return null;
};
