import { createReducer } from 'deox';
import { fetchAllClinicsRequest, fetchAllClinicsSuccess, fetchAllClinicsError } from './actions';
import { IClinic } from './types';

interface State {
  clinics: Array<IClinic>;
  isLoading: boolean;
  error: Error | null;
}

const defaultState: State = {
  clinics: [],
  isLoading: false,
  error: null,
};

const clinicsReducer = createReducer(defaultState, handle => [
  handle(fetchAllClinicsRequest, state => ({
    ...state,
    error: null,
    isLoading: true,
  })),
  handle(fetchAllClinicsSuccess, (state, { payload }) => ({
    clinics: payload.allClinics,
    error: null,
    isLoading: false,
  })),
  handle(fetchAllClinicsError, (state, { payload }) => ({
    ...defaultState,
    error: payload.error,
  })),
]);

export default clinicsReducer;
